body{
    width: 1920px;
    height: 100%;
    overflow: hidden;
    background-color: #fafafa;
    margin:0;
}
.left-area {
    box-sizing: border-box;
    width: 260px;
    height: 100%;
    overflow: visible;
    background-color: #ffffff;
    border-style: solid;
    border-color: #dedede;
    border-top-width: 0px;
    border-bottom-width: 0px;
    border-left-width: 0px;
    border-right-width: 1px;
    float: left;
  }
.left-area ul {
  list-style:none;
  padding:0px;
}
.left-area a{
  color:black;
  text-decoration:none;
}
.left-area a:visited {
  color:black;
  text-decoration:none;
}
.content-area {
    width: 1660px;
    height: 100%;
    overflow: visible;
    background-color: rgba(255, 255, 255, 0);
    float: left;
}
.desin_system {
    width: 97px;
    height: 84px;
    overflow: visible;
    font-family: "Inter", sans-serif;
    color: #000000;
    font-size: 24px;
    letter-spacing: 0px;
    line-height: 1.2;
    font-weight: 300;
    font-style: normal;
    margin-top: 43px ;
    margin-left: 47px;
  }
  .style_guide {
    width: 92px;
    height: 15px;
    overflow: visible;
    font-family: "Inter-Medium", "Inter", sans-serif;
    color: #000000;
    font-size: 14px;
    letter-spacing: 0px;
    line-height: 1.1;
    font-weight: 500;
    margin-top: 64px ;
    margin-left: 47px;
  }
  .style_guide_elements {
    width: 128px;
    height: 133px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    overflow: visible;
    margin-top: 25px ;
