  }
  .ui_component {
    width: 98px;
    height: 15px;
    overflow: visible;
    font-family: "Inter-Medium", "Inter", sans-serif;
    color: #000000;
    font-size: 14px;
    letter-spacing: 0px;
    line-height: 1.1;
    font-weight: 500;
    margin-top: 35px ;
